@import url(https://fonts.googleapis.com/css2?family=Philosopher:wght@400;700&display=swap);
/* purgecss start ignore */

*{
  margin:0;
  padding:0;
  box-sizing:border-box
}

html{
  --scrollbarBG: #ffe4c4;
  --thumbBG: #ffa600ef
}

body::-webkit-scrollbar{
  width:0.8rem
}

body{
  scrollbar-width:thin;
  scrollbar-color:#ffa600ef #ffe4c4;
  scrollbar-color:var(--thumbBG) var(--scrollbarBG);
  scrollbar-color:#ffa600ef;
  scrollbar-color:var(--thumbBG)
}

body::-webkit-scrollbar-track{
  background:#ffe4c4;
  background:var(--scrollbarBG)
}

body::-webkit-scrollbar-thumb{
  background-color:#ffa600ef;
  background-color:var(--thumbBG);
  border-radius:6px;
  border:3px solid #ffe4c4;
  border:3px solid var(--scrollbarBG)
}

::selection{
  color:whitesmoke;
  background:#ff8a00
}

html,body{
  overflow-x:hidden;
  font-family:'Philosopher', sans-serif
}

.main .innerContainer{
  background:linear-gradient(to right top, #000 0%, rgba(0,0,0,0) 75.56%)
}

.innerContainer::-webkit-scrollbar{
  display:none
}

.innerContainer{
  -ms-overflow-style:none;
  scrollbar-width:none
}

*{
  -webkit-tap-highlight-color:transparent;
  outline:none;
  touch-action:manipulation
}

