.heroSection{
  width:100%;
  height:100vh;
  position:relative;
  color:#f2f2f2;
  font-family:'Philosopher', sans-serif
}

.heroSection .innerHeroContainer{
  width:100%;
  height:100%;
  display:flex;
  align-items:center;
  padding:6rem
}

.heroSection .innerHeroContainer .scrollDown .textContainer{
  position:absolute;
  left:50%;
  bottom:9.5%;
  -webkit-transform:translateX(-50%);
  transform:translateX(-50%)
}

@media (max-width: 425px){
  .heroSection .innerHeroContainer .scrollDown .textContainer{
    bottom:11.5%
  }
}

.heroSection .innerHeroContainer .scrollDown .textContainer h1{
  font-size:1.5rem;
  font-weight:normal
}

@media (max-width: 425px){
  .heroSection .innerHeroContainer .scrollDown .textContainer h1{
    font-size:1.2rem
  }
}

.heroSection .innerHeroContainer .scrollDown a{
  cursor:pointer
}

.heroSection .innerHeroContainer .scrollDown a .arrow,.heroSection .innerHeroContainer .scrollDown a .arrow:before{
  position:absolute;
  left:50%
}

.heroSection .innerHeroContainer .scrollDown a .arrow{
  width:25px;
  height:25px;
  bottom:5%;
  margin:-20px 0 0 -20px;
  transform:rotate(45deg);
  -webkit-transform:rotate(45deg);
  border-left:none;
  border-top:none;
  border-right:2px #fff solid;
  border-bottom:2px #fff solid
}

@media (max-width: 425px){
  .heroSection .innerHeroContainer .scrollDown a .arrow{
    bottom:7%
  }
}

@media (max-width: 769px){
  .heroSection .innerHeroContainer .scrollDown a .arrow{
    width:19px;
    height:19px
  }
}

.heroSection .innerHeroContainer .scrollDown a .arrow:before{
  content:'';
  width:10px;
  height:10px;
  top:50%;
  margin:-10px 0 0 -10px;
  border-left:none;
  border-top:none;
  border-right:1px #fff solid;
  border-bottom:1px #fff solid;
  -webkit-animation-duration:2s;
  animation-duration:2s;
  -webkit-animation-iteration-count:infinite;
  animation-iteration-count:infinite;
  -webkit-animation-name:arrow;
  animation-name:arrow
}

@media (max-width: 769px){
  .heroSection .innerHeroContainer .scrollDown a .arrow:before{
    width:8px;
    height:8px
  }
}

@-webkit-keyframes arrow{
  0%{
    opacity:1
  }

  100%{
    opacity:0;
    -webkit-transform:translate(-8px, -8px);
    transform:translate(-8px, -8px)
  }
}

@keyframes arrow{
  0%{
    opacity:1
  }

  100%{
    opacity:0;
    -webkit-transform:translate(-8px, -8px);
    transform:translate(-8px, -8px)
  }
}

